
#title {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 45px;
  z-index: 999;
  color: white;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 95%);
}
#title .back {
  width: 15px;
}
#title .back img {
  width: 19px;
  position: absolute;
  left: 8px;
  top: 12px;
}
#title .share img {
  width: 23px;
  position: absolute;
  right: 12px;
  top: 10px;
}
#title .t_content {
  display: inline-block;
  margin-top: 10px;
  font-size: 17px;
  color: white;
}



