
.active {
  color: yellow !important;
}
#header {
  width: 100%;
  height: 2.8rem;
  z-index: 9;
  position: fixed;
  top: 0;
}
#header img {
  width: 1.5rem;
  position: absolute;
  top: 20%;
  left: 5%;
}
#header .p_title {
  text-align: center;
  color: white;
  margin: 9px;
  font-weight: bold;
}
.aside {
  position: fixed;
  z-index: 11;
  top: 0;
  left: 0;
  right: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
}
.aside::-webkit-scrollbar {
  display: none!important;
  width: 0!important;
  height: 0!important;
  -webkit-appearance: none;
  opacity: 0!important;
}
.aside ul {
  margin: 0;
  float: left;
  width: 50%;
  height: 100%;
  overflow: auto;
  padding: 1.3rem 0.5rem 0.5rem;
  background: #222;
  opacity: 0.8;
  -webkit-transform: translate(-100%, 0);
          transform: translate(-100%, 0);
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  -webkit-overflow-scrolling: touch;
}
.aside ul::-webkit-scrollbar {
  display: none!important;
  width: 0!important;
  height: 0!important;
  -webkit-appearance: none;
  opacity: 0!important;
}
.aside ul li {
  cursor: pointer;
  font-size: 0.80rem;
  font-weight: bold;
  list-style: none;
  color: #fff;
  margin: 11% 9px;
  width: 140px;
  text-align: left;
}
.aside ul li .iconfont {
  float: right;
  font-size: 0.6rem;
}
.aside ul li.chose {
  color: #FFD300;
}
.aside ul li img:first-child {
  width: 20px;
  height: 20px;
  margin-right: 5px;
  vertical-align: -4px;
}
.aside ul li img:last-child {
  width: 20px;
  height: 20px;
  float: right;
  vertical-align: -4px;
}
.aside ul li div {
  display: inline-block;
  margin-top: 3px;
}
.aside .cover {
  width: 100%;
  height: 100%;
  opacity: 0;
  display: none;
  background: rgba(172, 185, 201, 0.4);
  transition: opacity 0.3s ease;
}
.aside.open ul {
  -webkit-transform: translate(0);
          transform: translate(0);
}
.aside.open .cover {
  opacity: 1;
}
.aside.docked {
  visibility: visible;
}
.aside.docked .cover {
  display: block;
}
.overflow-hidden {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  position: absolute;
}



