
.error_content {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  background: white;
}
.error_content .error_img {
  position: absolute;
  top: 30%;
  left: 50%;
  margin-left: -64px;
  margin-top: -64px;
}
.error_content .error_img img {
  width: 100%;
}
.error_content .error_click {
  width: 80%;
  position: absolute;
  bottom: 30%;
  left: 50%;
  margin-left: -128px;
}



