
.titleImg {
  width: 100%;
  /*height:270px;*/
  position: relative;
}
.titleImg img {
  width: 100%;
}
.titleImg p {
  position: absolute;
  width: 100%;
  text-align: right;
  margin: 0;
  bottom: 5px;
  right: 0;
  font-size: 1.5em;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 95%);
  color: white;
  padding-bottom: 5px;
}
.titleImg p span {
  margin-right: 10px;
}
._author {
  text-align: left;
  padding: 10px 0 10px 5%;
  background: white;
  margin-top: -4px;
  color: #222;
}
._author img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  vertical-align: sub;
  margin: 0 10px 0 20px;
}
.weui-cell {
  padding: 0 !important;
}
.vux-no-group-title {
  font-size: 14px;
  text-align: left;
}
.vux-label {
  margin: 0 4px 0 7px;
}



