
.d_loading {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 100;
}
.d_loading .spinner {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -25px 0 0 -25px;
}
.d_loading .loading_content {
  position: absolute;
  top: 49%;
  left: 50%;
  display: inline-block;
  margin-left: -78px;
  color: #959595;
  text-align: center;
}



