
.img_div {
  width: 100%;
  position: relative;
  margin-bottom: 15px;
}
.img_div img {
  width: 100%;
}
.img_div h3 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0;
  position: absolute;
  bottom: 0;
  padding: 8px 5px 0;
  color: white;
  font-size: 17px;
  font-family: "微软雅黑";
  width: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.7) 95%);
  height: 46px;
}
.content {
  font-size: 16px !important;
}
.headline .img-place-holder {
  height: 0 !important;
}
.active-img {
  position: fixed;
  top: 30%;
  left: 0;
  z-index: 20;
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  z-index: 999;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.dis-bg {
  background: #222;
  opacity: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 998;
  transition: opacity 1s;
}
.dis-none {
  display: none;
}



